import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import {  useContext } from "react";
 
// Components
import "react-responsive-carousel/lib/styles/carousel.min.css";
import Carousel from "react-bootstrap/Carousel";
import VideosItemAdmin from "../admin/videosItemAdmin";
import VideosCategoriaFila from "../videos/videosCategoriaFila";
import * as HistorialUserServer from "../../services/historialUser";
import * as PreferenciasUserServer from "../../services/preferenciasUser";
import Context from "../context/UserContext";

import VideoListSearch from "../videos/videoListSearch";
import "../../styles/styles.css";
const useStyles = makeStyles((theme) => ({
  root: {
  },
  "@media (max-width: 1248px)": {
    root: {
      display: "flex",
    },
  },
}));
/**
 * Componente para organizar en el index del admin los videos en su respectiva categorÃ­a, con un buscador y filtro.
 */
function VideosListAd({ videos, categories, search, search2, ...props }) {
  const history = useNavigate();

  const [histUsers, setHistUsers] = useState();
  const [prefUsers, setPrefUsers] = useState(null); 
  const [categoriasTotales, setCategoriasTotales] = useState(null);
  const { user } = useContext(Context)

  /**
   * FunciÃ³n para obtener el historial de los usuarios.
   */
  const getHistorialUsers = async () => {
    try {
      const res = await HistorialUserServer.ListHistorialUser({'user_id':user.id});
      setHistUsers(res);
    } catch (error) {
      console.log(error);
    }
  };
  /**
   * FunciÃ³n para obtener las preferencias de los usuarios
   */
  const getPreferenciasUser = async () => {
    try {
      const res = await PreferenciasUserServer.ListPreferenciaUser({'user_id':user.id});   
      setPrefUsers(res);
    } catch (error) {
      console.log(error);
    }
  };
  
  useEffect(() => {
    getHistorialUsers();
    getPreferenciasUser();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  
  /**
   * FunciÃ³n para organizar las categorias de videos dependiendo de las preferencias.
   */
  useEffect(() => {

    if(prefUsers && categories) {
      let array = [];
      let array1 =[];
      let array2 = [];

      for (let i = 0; i < prefUsers.length; i++) {
        array1.push(prefUsers[i].categoria);        
      } 
      for (let i = 0; i < categories.length; i++) {
        array2.push(categories[i]);        
      }    
      
      for (var i = 0; i < array2.length; i++) {
          var igual=false;
           for (var j = 0; j < array1.length & !igual; j++) {
               if(array2[i].categoria === array1[j] )
                igual=true;
              }
              array = prefUsers
          if(!igual)array.push(array2[i]);
      }
      
      if (array) {
        setCategoriasTotales(array);
      }
      else {
        setCategoriasTotales(null);
      }
      
    }
  },[categories, prefUsers])
  /**
   * FunciÃ³n para redireccionar al reproductor del video seleccionado, se crea un nuevo historial de usuario si es la primera vez en ver el video, de lo contrario se envÃ­a este historial como state
   * @param {*} id 
   */
  const verVideo = async (id) =>{
    const formData = new FormData();
    let histUser;
    let contador = 0;
    formData.append("usuario", user.id);
    formData.append("video", id);
    try {
      for (let index = 0; index < histUsers.length; index++) {
        const element = histUsers[index];
        if (element.usuario.id === user.id && element.video === id) {
          histUser = element;
          contador++;
        }
      }
      if (contador === 0) {
        const hu = await HistorialUserServer.RegisterHistorialUser(formData);
        histUser = hu.data;
      }
    } catch (error) {
      console.log(error);
    }
    history(`/seeVideo/${id}`,{state:histUser});
    
  }
  
  const classes = useStyles();
  if (videos.length > 0 ) {
    return (
      <div className="">
        <div id="carousel_videos">
          {/* {console.log(props.listVideos)}
          {console.log(videos)} */}
         
          {/* <h2>ðŸ•‘ Ãšltimos videos Admin </h2> */}
          <Carousel className={classes.root} id="carousel" fade >
            {videos &&
              search(videos).slice(0, 6).map((video,index) => (
                <Carousel.Item key={index}>
                  <VideosItemAdmin
                    key={index}
                    video={video}
                    histUsers = {histUsers}
                    user = {user}
                    verVideo = {verVideo}
                  />
                </Carousel.Item>
              ))}
          </Carousel>
        </div>
        
        <br></br>
        <br></br>
        <br></br>
       <div id="card_videos" className="">
  {
    prefUsers ? 
      <div>
        {categoriasTotales && (() => {
          const categoriasFiltradas = search2(categoriasTotales);
          return categoriasFiltradas.map((element, index) => (
            categoriasFiltradas.length <= 1 ?
              <VideoListSearch 
                key={index}
                categoria={element}
                videos={videos}
                search={search}
                histUsers={histUsers}
                user={user}
                verVideo={verVideo}
              />
              :
              <VideosCategoriaFila
                key={index}
                categoria={element}
                videos={videos}
                search={search}
                histUsers={histUsers}
                user={user}
                verVideo={verVideo}
              />
          ));
        })()}
      </div>
    :
    categories && (() => {
      const categoriasFiltradas = search2(categories);
      return categoriasFiltradas.map((element, index) => (
        categoriasFiltradas.length <= 1 ?
          <VideoListSearch 
            key={index}
            categoria={element}
            videos={videos}
            search={search}
            histUsers={histUsers}
            user={user}
            verVideo={verVideo}
          />
          :
          <VideosCategoriaFila
            key={index}
            categoria={element}
            videos={videos}
            search={search}
            histUsers={histUsers}
            user={user}
            verVideo={verVideo}
          />
      ));
    })()
  }
</div>

      </div>
    );
  }
  else {
    return (
      <div id="card_videos m-5 text-center">
        <h2 style={{color:"white"}}><center>No hay videos disponibles</center></h2>
      </div>
    )
  }
}

export default VideosListAd;
